body {
    background-color: #1b1c21;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
    font-family: "Roboto";
    text-decoration: none;
    line-height: 150%;
}


main{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    padding: 20px;

    .main-content{
        background-color: #FFF;
        max-height: 768px;
        width: 100%;
        max-width: 1024px;
        border-radius: 5px;
        box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
        display: flex;
        align-items: center;

        @media(max-width: 950px){
            flex-direction: column-reverse; 
            height: 100%;
            overflow: auto;
            height: auto;
        }

        .img-laptop{  
            max-height: 668px;
            @media(max-width: 950px){
                display: none;
            }
        }

        .text{
            padding: 50px;
            @media(max-width: 950px){
                padding: 10px;
            }

            h1{
                font-size: 24px;
                font-weight: bold;
                color: #1b1c21;
                margin-bottom: 20px;

                @media(max-width: 950px){
                    font-size: 20px;
                }
            }

            p{
                font-size: 16px;
                color: #1b1c21;
                margin-bottom: 20px;
            }

            form{
                display: flex;
                flex-direction: column;
                width: 100%;

                input{
                    height: 40px;
                    border-radius: 5px;
                    border: 1px solid #1b1c21;
                    padding: 0 10px;
                    margin-bottom: 20px;
                }

                button{
                    height: 40px;
                    border-radius: 5px;
                    border: 1px solid #1b1c21;
                    background-color: #1b1c21;
                    color: #FFF;
                    font-weight: bold;
                    cursor: pointer;
                }
            }

            .ending{
                display: flex;
                align-items: center;
                margin-bottom: 20px;

                .logo{
                    height: 80px;
                    margin-right: 20px;

                    @media(max-width: 950px){     
                    height: 60px;
                    }
                }

                h2{
                    font-family: cozy-caps;
                }
            }
        }
    }
}
  .spinner {
    display: inline-block;
    width: 80px;
    height: 80px;
  }
  
  .spinner:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fff;
    border-color: #ecc32a transparent #000 transparent;
    animation: spinner 1.2s linear infinite;
  }
  
  @keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
}

.download{
    width: 100%;
    text-align: center;
    padding: 14px;
    text-transform: uppercase;
    display: block;
    border-radius: 5px;
    border: 1px solid #1b1c21;
    background-color: #1b1c21;
    color: #FFF;
    font-weight: bold;
    cursor: pointer;
}